.search-controls {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.search-controls__item {
  padding-right: 20px;
}

.sort-dropdown {
  white-space: nowrap;
}

.reset-password-modal .modal-body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.new-password {
  margin-top: 20px;
}

.report-download { 
  display: flex;
  align-items: center;
  margin-left: auto;
}

.report-download .button {
  margin: 0 0 0 10px;
}

.search__no-results {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 143px;
  right: 0;
  left: 0;
  bottom: 0;
}

.search__no-results .inline-message {
  font-size: 1.25rem;   
}